/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-wpinc-cards {
	padding: 1.25rem;

	outline      : dotted 2px var(--wp-admin-theme-color);
	border-radius: 1px;

	position: relative;
	&::before {
		content         : attr(data-container-label);
		position        : absolute;
		top             : 0;
		left            : 0;
		background-color: #fff;
		border          : 2px solid #fff;
		color           : var(--wp-admin-theme-color);
		font-size       : 0.75rem;
		line-height     : 1;
	}

	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: grid;
		gap    : 1rem;
	}

	&.card-2 > .block-editor-inner-blocks > .block-editor-block-list__layout {
		--nc-container-min-width: 16.5rem;

		grid-template-columns: repeat(
			auto-fill,
			minmax(
				var(--nc-container-min-width),
				1fr
			)
		);
	}
	&.card-3 > .block-editor-inner-blocks > .block-editor-block-list__layout {
		--nc-container-min-width: 12rem;

		grid-template-columns: repeat(
			auto-fill,
			minmax(
				clamp(
					var(--nc-container-min-width),
					(var(--nc-container-min-width) * 3 + 1rem * 2 - 100%) * 9999,
					51%
				),
				1fr
			)
		);
	}
	&.card-4 > .block-editor-inner-blocks > .block-editor-block-list__layout {
		--nc-container-min-width: 10rem;

		grid-template-columns: repeat(
			auto-fill,
			minmax(
				clamp(
					var(--nc-container-min-width),
					(var(--nc-container-min-width) * 4 + 1rem * 3 - 100%) * 9999,
					34%
				),
				1fr
			)
		);
	}
}
